body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

:root[data-theme="light"] {
  --primary: rgb(2 107 93);
  --surface-tint: rgb(2 107 93);
  --on-primary: rgb(255 255 255);
  --primary-container: rgb(159 242 224);
  --on-primary-container: rgb(0 32 27);
  --secondary: rgb(74 99 93);
  --on-secondary: rgb(255 255 255);
  --secondary-container: rgb(205 232 224);
  --on-secondary-container: rgb(6 32 27);
  --tertiary: rgb(68 98 120);
  --on-tertiary: rgb(255 255 255);
  --tertiary-container: rgb(201 230 255);
  --on-tertiary-container: rgb(0 30 47);
  --error: rgb(186 26 26);
  --on-error: rgb(255 255 255);
  --error-container: rgb(255 218 214);
  --on-error-container: rgb(65 0 2);
  --background: rgb(245 251 247);
  --on-background: rgb(23 29 27);
  --surface: rgb(245 251 247);
  --on-surface: rgb(23 29 27);
  --surface-variant: rgb(218 229 225);
  --on-surface-variant: rgb(63 73 70);
  --outline: rgb(111 121 118);
  --outline-variant: rgb(190 201 197);
  --shadow: rgb(0 0 0);
  --scrim: rgb(0 0 0);
  --inverse-surface: rgb(43 50 48);
  --inverse-on-surface: rgb(236 242 239);
  --inverse-primary: rgb(131 213 196);
  --primary-fixed: rgb(159 242 224);
  --on-primary-fixed: rgb(0 32 27);
  --primary-fixed-dim: rgb(131 213 196);
  --on-primary-fixed-variant: rgb(0 80 69);
  --secondary-fixed: rgb(205 232 224);
  --on-secondary-fixed: rgb(6 32 27);
  --secondary-fixed-dim: rgb(177 204 197);
  --on-secondary-fixed-variant: rgb(51 75 70);
  --tertiary-fixed: rgb(201 230 255);
  --on-tertiary-fixed: rgb(0 30 47);
  --tertiary-fixed-dim: rgb(171 202 228);
  --on-tertiary-fixed-variant: rgb(43 74 96);
  --surface-dim: rgb(213 219 216);
  --surface-bright: rgb(245 251 247);
  --surface-container-lowest: rgb(255 255 255);
  --surface-container-low: rgb(239 245 242);
  --surface-container: rgb(233 239 236);
  --surface-container-high: rgb(227 234 230);
  --surface-container-highest: rgb(222 228 225);
}

:root[data-theme="dark"] {
  --primary: rgb(131 213 196);
  --surface-tint: rgb(131 213 196);
  --on-primary: rgb(0 56 47);
  --primary-container: rgb(0 80 69);
  --on-primary-container: rgb(159 242 224);
  --secondary: rgb(177 204 197);
  --on-secondary: rgb(28 53 48);
  --secondary-container: rgb(51 75 70);
  --on-secondary-container: rgb(205 232 224);
  --tertiary: rgb(171 202 228);
  --on-tertiary: rgb(18 51 72);
  --tertiary-container: rgb(43 74 96);
  --on-tertiary-container: rgb(201 230 255);
  --error: rgb(255 180 171);
  --on-error: rgb(105 0 5);
  --error-container: rgb(147 0 10);
  --on-error-container: rgb(255 218 214);
  --background: rgb(14 21 19);
  --on-background: rgb(222 228 225);
  --surface: rgb(14 21 19);
  --on-surface: rgb(222 228 225);
  --surface-variant: rgb(63 73 70);
  --on-surface-variant: rgb(190 201 197);
  --outline: rgb(137 147 143);
  --outline-variant: rgb(63 73 70);
  --shadow: rgb(0 0 0);
  --scrim: rgb(0 0 0);
  --inverse-surface: rgb(222 228 225);
  --inverse-on-surface: rgb(43 50 48);
  --inverse-primary: rgb(2 107 93);
  --primary-fixed: rgb(159 242 224);
  --on-primary-fixed: rgb(0 32 27);
  --primary-fixed-dim: rgb(131 213 196);
  --on-primary-fixed-variant: rgb(0 80 69);
  --secondary-fixed: rgb(205 232 224);
  --on-secondary-fixed: rgb(6 32 27);
  --secondary-fixed-dim: rgb(177 204 197);
  --on-secondary-fixed-variant: rgb(51 75 70);
  --tertiary-fixed: rgb(201 230 255);
  --on-tertiary-fixed: rgb(0 30 47);
  --tertiary-fixed-dim: rgb(171 202 228);
  --on-tertiary-fixed-variant: rgb(43 74 96);
  --surface-dim: rgb(14 21 19);
  --surface-bright: rgb(52 59 57);
  --surface-container-lowest: rgb(9 15 14);
  --surface-container-low: rgb(23 29 27);
  --surface-container: rgb(27 33 31);
  --surface-container-high: rgb(37 43 41);
  --surface-container-highest: rgb(48 54 52);
}

body {
  color: var(--on-background);
  background: var(--background);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  display: flex;
  height: 100vh;
  width: 100%;
  overflow: hidden
}

.main {
  width: 100%;
  padding: 1rem;
}

nav {
  width: 96px;
  background: var(--surface-container);
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  padding: 2.5rem 1.56rem 2.5rem 1.56rem;
}

nav ul {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.NavItem {
  display: flex;
  flex-direction: column;
  align-items: center;
}

nav a {
  width: 100%;
  text-decoration: none;
  display: flex;
  justify-content: center;
  color: var(--on-surface-variant);
}

.navigationIconContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 56px;
  height: 32px;
}

nav a.active {
  color: var(--on-surface);
}

nav a.active .navigationIconContainer {
  background-color: var(--secondary-container);
  border-radius: 25px;
}

nav a svg path {
  fill: var(--on-surface-variant);
}

nav a.active svg path {
  fill: var(--on-secondary-container);
}

.select-box {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  min-height: 60px;
  font-size: 14px;
  color: var(--on-surface-variant);
  width: 200px;
  text-align: left;
}

.custom-select {
  width: 200px;
  margin-bottom: 15px;
  padding: 4px;
  border-bottom: 1px solid var(--on-surface);
}

.custom-select .option {
  padding: 4px 0;
  padding-left: 20px;
  text-align: left;
}

.custom-select .option:hover {
  background: var(--primary-container);
  border-radius: 20px;
}

.select-box span {
  font-size: 16px;
  color: var(--on-surface);
}

.select-box .options-toggle {
  height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 10px;
  cursor: pointer;
}

.row {
  display: flex;
}

.column {
  display: flex;
  flex-direction: column;
}

.text-center {
  text-align: center;
}

.mainLogo {
  display: flex;
  align-items: center;
  font-size: 28px;
  gap: 6px;
  font-weight: bold;
  justify-content: center;
}

button {
  border: none;
  color: var(--on-primary);
  padding: 10px 24px;
  background: var(--primary);
  border-radius: 100px;
}

.form-default {
  width: 200px;
  display: flex;
  flex-direction: column;
  gap: 8px;
  align-items: center;
}

.icon-button {
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  background-color: var(--primary-container);
  padding: 10px;
}

nav .icon-button {
  background-color: var(--surface-container-highest);
}

.custom-select path {
  fill: var(--on-surface-variant);
}

.logo {
  width: 75px;
}

.logo .logo-primary {
  fill: var(--on-primary-container);
}

html[data-theme="dark"] .logo .logo-primary {
  fill: var(--primary-container);
}

.logo .logo-star {
  fill: var(--primary);
}
